@media (max-width: 900px) {
  .itemsContainer {
    width: 400px;
  }
  .titleContainer {
    margin: 30px 0;
    grid-template: "sectionTitle1 viewMore2";
    grid-template-columns: auto 1fr;
    justify-content: start;
    align-items: center;
  }
  .viewMore_link {
    margin-left: 30px;
  }
  .sliderItem {
    display: none;
  }
}//900

@media (max-width: 480px) {
  .item {
    width: 280px;
  }
}//480

@media (max-width: 440px) {
  .titleContainer {
    margin: 30px 0;
    grid-template: "sectionTitle1" "viewMore2";
    justify-content: start;
    align-items: center;
  }
  .viewMore_link {
    margin-left: 0px;
  }
}//440
